const SET_GLOBAL_MESSAGE = 'HELP/SET_GLOBAL_MESSAGE';
const CLEAR_HELP = 'HELP/CLEAR_HELP';
const SET_UPLOADING = 'HELP/SET_UPLOADING';
const SET_PRODUCTS = 'HELP/SET_PRODUCTS';
const SET_FCS = 'HELP/SET_FCS';
const SET_CURRENT_FC = 'HELP/SET_CURRENT_FC';
const SET_FROM_DATE = 'HELP/SET_FROM_DATE';
const SET_TO_DATE = 'HELP/SET_TO_DATE';

export const setGlobalMessage = (data) => ({ type: SET_GLOBAL_MESSAGE, data});
export const clearHelp = () => ({ type: CLEAR_HELP });
export const setUploading = (data) => ({ type:  SET_UPLOADING, data });
export const setProducts = (data) => ({ type: SET_PRODUCTS, data });
export const setFcs = (data) => ({ type: SET_FCS, data });
export const setCurrentFc = (data) => ({ type: SET_CURRENT_FC, data});
export const setFromDate = (data) => ({ type: SET_FROM_DATE, data  })
export const setToDate = (data) => ({ type: SET_TO_DATE, data  })

const initialState = {
    globalMessage: null,
    uploading: false,
    products: [],
    fcs: [],
    currentFc: 'All',
    fromDate: null,
    toDate: null
}

export default function Help(state = initialState, action) {
    switch (action.type) {
        case CLEAR_HELP:
            return {
                ...state,
                globalMessage: null,
                uploading: false
            }
        case SET_UPLOADING: 
            return {
                ...state,
                uploading: !!action.data? action.data: false
            }
        case SET_GLOBAL_MESSAGE:
            return {
                ...state, 
                globalMessage: !!action.data? {...action.data}: null,
            }
        case SET_PRODUCTS:
            return {
                ...state,
                products: !!action.data? [].concat(action.data): []
            }
        case SET_FCS:
            return {
                ...state,
                fcs: !!action.data? [...action.data]: []
            }
        case SET_CURRENT_FC:
            return {
                ...state,
                currentFc: !!action.data? action.data: 'All'
            }
        case SET_FROM_DATE:
            return {
                ...state,
                fromDate: !!action.data? action.data: null
            }
        case SET_TO_DATE:
            return {
                ...state,
                toDate: !!action.data? action.data: null
            }
        default: 
            return state;
    }
}